<template>
    <div class="mechanism">
        <div class="mechTop">
            <div class="topTitle">
                <p>经典案例</p>
            </div>
        </div>
        <div class="mechContent">
            <el-container>
                <el-header style="text-align: center;height: 40px;padding:0">
                    <div class="yijiTitle">经典案例</div>
                </el-header>
                <el-main style="padding: 0">
                    <div class="checkBox">
                        <!-- <div style="margin-bottom: 15px">
                            <p style="width:100px; float:left;margin: 0">纠纷焦点：</p>
                            <el-checkbox-group v-model="checkedCities">
                                <el-checkbox v-for="(item, index) in showdetailList" :label="item.id" :key="index">{{item.name}}</el-checkbox>
                            </el-checkbox-group>
                            <p v-if ="cities.length>6" @click = "showTag"><span>{{flag?"展开":"收起"}}</span></p>
                        </div> -->
                        <!-- <div style="margin-bottom: 15px">
                            <p style="width:100px; float:left;margin: 0">纠纷焦点：</p>
                            <el-checkbox-group v-model="checkedCities">
                                <el-checkbox v-for="(item, index) in cities" :label="item.id" :key="index">{{item.name}}</el-checkbox>
                            </el-checkbox-group>
                            <p v-if ="cities.length>6" @click = "showTag"><span>{{flag?"展开":"收起"}}</span></p>
                        </div> -->
                        <div style="margin-bottom: 15px;line-height: 30px;">
                            <p style="width:100px; float:left;margin: 0">纠纷焦点：</p>
                            <el-checkbox-group v-model="checkedCities" style="float:left;width:70%">
                                <el-checkbox v-for="(item, index) in showdetailList" :label="item.id" :key="index">{{item.name}}</el-checkbox>
                            </el-checkbox-group>
                            <el-button size='mini' type="primary" round style="float:right" v-if ="cities.length>6" @click = "showTag">{{flag?"展开":"收起"}}</el-button>
                            <div style="clear:both"></div>
                        </div>
                    </div>
                    <div class="richText">
                        <h2>第一章 总则</h2>
                        <span>发布时间:2019-12-23  来源：本站原创  作者：佚名 </span>
                        <div class="news_content">
                            <h2 style="text-align: left;text-indent: 2em;font-weight: 700;font-size: 18px;width: 112px;float:left">人民调解</h2><span>是指人民调解委员会通过说服、疏导等方法，促使当事人在平等协商基础上自愿达成</span>
                            <p>调解协议，解决纠纷的活动。</p>
                            <p>调解原则:</p>
                            <p>(一)在当事人自愿、平等的基础上进行调解;</p>
                            <p>(二)不违背法律、法规和国家政策;</p>
                            <p>(三)尊重当事人的权利，不得因调解而阻止当事人依法通过仲裁、行政、司法等途径维护自己的权利。</p>
                            <p>申请调解所需提供材料：</p>
                            <p>患方：</p>
                            <p>1、调解申请书</p>
                            <p>2、法定代表人身份证及证明书</p>
                            <p>3、委托代理人授权委托书</p>
                            <p>4、委托代理人身份证原件及复印件</p>
                            <p>5、申诉材料</p>
                            <p>6、纠纷相关证据材料。</p>
                            <p>医方：</p>
                            <p>1、调解申请书 </p>
                            <p>2、法定代表人身份证及证明书</p>
                            <p>3、委托代理人授权委托书 </p>
                            <p>4、委托代理人身份证原件及复印件 </p>
                            <p>5、执业许可证复印件 </p>
                            <p>6、当事人执业证及身份证复印件 </p>
                            <p>7、医疗纠纷发生经过及调查处理情况。</p>
                            <p>8、针对患方提出的问题和要求给予书面回复。</p>
                            <p>9、纠纷相关证据材料。</p>
                        </div>
                    </div>
                </el-main>
            </el-container>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return{
            checkedCities: [],
            cities: [
                {name: '第一章', id: '1'},
                {name: '第一章', id: '2'},
                {name: '总则', id: '3'},
                {name: '总则', id: '4'},
                {name: '第则', id: '5'},
                {name: '章则', id: '6'},
                {name: '章则', id: '6'},
                {name: '章则', id: '6'},
                {name: '章则', id: '6'},
                {name: '章则', id: '6'},
                {name: '章则', id: '6'},
                {name: '章则', id: '6'},
            ],
            flag: true
        }
    },
    computed: {
        showdetailList: {
        get: function () {
            if (this.flag) {
                if (this.cities.length < 7) {
                    return this.cities
                }
                let newArr = []
                for (var i = 0; i < 6; i++) {
                    let item = this.cities[i]
                    newArr.push(item)
                }
                return newArr
            }
            return this.cities
            },
            set: function (val) {
                this.showdetailList = val
            }
        }
    },
    methods: {
        showTag() {
            this.flag = !this.flag
        }
    }
}
</script>>
<style scoped>
.mechTop{
    width: 100%;
    height: 315px;
    background-image: url(../assets/mechanism.png);
    background-repeat:no-repeat;
    background-size: cover;
    position: relative;
}
.topTitle{
    height: 90px;
    background-color: #fff;
    margin: 0 10%;
    position: absolute;
    bottom: 0;
    left: 10%;
    right: 10%;
    border: 1px solid rgb(227, 227, 227);
}
.topTitle p{
    text-align: left;
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 20px;
    color: rgb(16, 16, 16);
    margin: 0 35px;
    line-height: 90px
}
.mechContent{
    padding: 30px 20%;
}
.checkBox{
    text-align: left;
    margin-bottom: 30px;
    padding: 20px;
    border: 1px solid #ccc;
    border-top: none 
}
.yijiTitle{
    height: 40px;
    background-color: rgb(241, 241, 241);
    border-left: 3px solid rgb(13, 114, 183);
    text-align: left;
    padding-left: 15px;
    line-height: 40px;
    font-weight: 400;
    font-size: 16px;
    color: rgb(16, 16, 16);
}
.richText{
    padding-top: 20px;
    background-color: rgb(248, 248, 248);
}
.richText h2 {
    margin: 0
}
.serviceBox{
    padding: 0 20px
}
.news_content{
    border-top: 1px dashed #999;
    margin: 10px 40px;
    padding-top: 20px;
    text-align: left;
    font-weight: 400;
    font-size: 16px;
    color: rgb(16, 16, 16);
    text-indent: 2em;
}
</style>


